<template>
  <div class="animated fadeIn">
    <CalendarHeader :activeDate="activeDate" :showFilters="true" :curRion="rigion" :curImport="importance" @resetDate="changeDate" @setRion="setRion" @setImport="setImport"></CalendarHeader>
    <div class="calendar-container">
      <div class="date-select">
        <!-- <ul>
          <li v-for="(item, index) in dateList" :data-date="activeDate" :class="{active: item == activeDate.toString()}" @click="changeDate(item, index)">
            <p><span class="date">{{item.getDate()}}</span> {{item | weekFormat}}</p>
            <p>{{item | monthFormat}}</p>
          </li>
        </ul> -->
        <div v-for="(item, index) in dateList" :class="{active: index == dayOfDate}" @click="changeDate(item, index)">
          <p><span class="date">{{item.getDate()}}</span> {{item | weekFormat}}</p>
          <p>{{item | monthFormat}}</p>
        </div>
      </div>
      <div class="calendar-block">
        <h3 class="title">财经数据</h3>
        <div class="finance-tips" v-if="financeData.length == 0">
          <img src="./assets/404-tips.png" alt="">
          <p>今日无财经数据</p>
        </div>
        <div class="finance-data-item" v-for="item in financeData">
          <div class="region-pic">
            <img :src="item.imgSrc" alt="">
          </div>
          <div class="finance-detail">
            <div class="finance-detail-row">
              <span class="time">{{item.time}}</span>
              <span class="level">
                <i v-for="n in item.importance"></i>
              </span>
              <span class="state">{{item.isPublish ? '已公布' : '未公布'}}</span>
            </div>
            <div class="finance-detail-row">
              {{item.name}}
            </div>
            <div class="finance-detail-row forecast">
              <span>今值：{{item.publishVal}}</span>
              <span>预测：{{item.forecastVal}}</span>
              <span>前值：{{item.oldVal}}</span>
            </div>
          </div>
          <div class="finance-influence">
            <div class="text-rise" v-if="item.benifit != ''">{{item.benifit}}</div>
            <div v-else="item.benifit != ''">--</div>
            <div class="text-drop" v-if="item.deBenifit != ''">{{item.deBenifit}}</div>
            <div v-else="item.deBenifit != ''">--</div>
          </div>
        </div>
      </div>
      <div class="calendar-block">
        <h3 class="title">财经大事件</h3>
        <div class="finance-tips" v-if="financeEvent.length == 0">
          <img src="./assets/404-tips.png" alt="">
          <p>今日无财经大事件</p>
        </div>
        <div class="finance-data-item" v-else v-for="item in financeEvent">
          <div class="region-pic">
            <img :src="item.imgSrc" alt="">
          </div>
          <div class="finance-detail">
            <div class="finance-detail-row">
              <span class="time">{{item.time | timeFormat | lengthFormat}}</span>
            </div>
            <div class="finance-detail-row">
              {{item.name}}
            </div>
          </div>
        </div>
      </div>
      <div class="calendar-block">
        <h3 class="title">假期休市一览</h3>
        <div class="finance-tips" v-if="financeOff.length == 0">
          <img src="./assets/404-tips.png" alt="">
          <p>今日无假期休市安排</p>
        </div>
        <div class="finance-data-item" v-else v-for="item in financeOff">
          <div class="region-pic">
            <img :src="item.imgSrc" alt="">
          </div>
          <div class="finance-detail">
            <div class="finance-detail-row">
              <span class="time">{{item.title}}</span>
            </div>
            <div class="finance-detail-row">
              {{item.content}}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Filter from './utils/filters.js';
import CalendarHeader from './components/CalendarHeader.vue'

export default {
  data () {
    return {
      financeData: [],
      financeEvent: [],
      financeOff: [],
      dateList: [],
      activeDate: new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate()),
      importance: '全部',
      rigion: '全部'
    }
  },

  computed: {
    curDate(){
      return this.activeDate.getDate();
    },
    curMonth(){
      return this.activeDate.getMonth();
    },
    curYear(){
      return this.activeDate.getFullYear();
    },
    dayOfDate(){
      return this.activeDate.getDay();
    }
  },
  methods: {
    setDateList(reRender){
      if(reRender){
        this.dateList.length = 0;
      }
      for (var i = 0; i < 7; i++) {
        this.dateList.push(new Date(this.curYear, this.curMonth, this.curDate - this.dayOfDate + i));
      }
    },
    getFinanceData(reRender){
      if(reRender){
        this.financeData.length = 0;
      }
      this.sendReq('I0017', {
        orderBy: "PublishDate,PublishTime asc",
        pageIndex: 1,
        pageSize: 100,
        region: encodeURI(this.rigion == '全部' ? '' : this.rigion),
        Importance: encodeURI(this.importance == '全部' ? '' : this.importance),
        EventDate: Filter.dateFormat(this.activeDate),
        isPublish: ''
      })
      .then((res) => {
        if(res.data.StatusCode == 0){
          if(res.data.data == null){
            return ;
          }
          res.data.data.forEach((item, index) => {
            this.financeData.push({
              name: item.KeyCode,
              isPublish: item.IsPublish,
              importance: item.Importance,
              time: item.publishTime.substring(0, 5),
              benifit: item.Benefit,
              deBenifit: item.DeBenefit,
              imgSrc: require('./assets/region/' + item.Region + '.png'),
              publishVal: item.publishValue || '--',
              forecastVal: item.ForecastValue || '--',
              oldVal: item.PreviousValue || '--'
            })
          })
        }
      })
      .catch((err) => { console.log(err); });
    },
    getFinanceEvent(reRender){
      if(reRender){
        this.financeEvent.length = 0;
      }
      this.sendReq('I0007', {
        orderBy: "PubTime asc",
        pageIndex: 1,
        pageSize: 50,
        region: encodeURI(this.rigion == '全部' ? '' : this.rigion),
        Importance: encodeURI(this.importance == '全部' ? '' : this.importance),
        date: Filter.dateFormat(this.activeDate),
        isPublish: ''
      })
      .then((res) => {
        // console.log(res);
        if(res.data.StatusCode == 0){
          if(res.data.data == null){
            return ;
          }
          res.data.data.forEach((item, index) => {
            this.financeEvent.push({
              name: item.EventName,
              time: new Date(parseInt(item.PubTime.match(/\d+/)[0])),
              imgSrc: require('./assets/region/' + item.Region + '.png'),
              importance: item.Importance
            })
          })
        }
      })
      .catch((err) => { console.log(err); });
    },
    getFinanceOff(reRender){
      if(reRender){
        this.financeOff.length = 0;
      }
      this.sendReq('I0018', {
        orderBy: "Time desc",
        pageIndex: 1,
        pageSize: 10,
        time: Filter.dateFormat(this.activeDate)
      })
      .then((res) => {
        // console.log(res);
        if(res.data.StatusCode == 0){
          if(res.data.data == null){
            return ;
          }
          res.data.data.forEach((item, index) => {
            this.financeOff.push({
              title: item.Reason,
              content: item.Detail,
              imgSrc: require('./assets/region/' + item.Region + '.png'),
            })
          })
        }
      })
      .catch((err) => { console.log(err); });
    },
    changeDate(o, i){
      this.activeDate = o;
      this.getFinanceData(true);
      this.getFinanceEvent(true);
      this.getFinanceOff(true);
      this.setDateList(true);
    },
    setRion(o){
      this.rigion = o;
      this.getFinanceData(true);
      this.getFinanceEvent(true);
      this.getFinanceOff(true);
    },
    setImport(o){
      this.importance = o;
      this.getFinanceData(true);
      this.getFinanceEvent(true);
      this.getFinanceOff(true);
    }
  },

  filters: {
    weekFormat(value){
      var weekIndex = value.getDay();
      var weekArr = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'];
      return weekArr[weekIndex];
    },
    monthFormat(value){
      var monthIndex = value.getMonth();
      var monthArr = ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'];
      return monthArr[monthIndex];
    },
    lengthFormat(value){
      return value.substring(0, 5);
    }
  },

  mounted(){
    
  },

  created(){
    this.getFinanceData();
    this.getFinanceEvent();
    this.getFinanceOff();
    this.setDateList();
  },

  components: {
    CalendarHeader
  }
}
</script>

<style lang="scss">
@import "./style/mixin";
.calendar-container {
  background-color: #f7f7f7;
}
.date-select {
  display: flex;
  width: 100%;
  align-items: center;
  background-color: #eef6fe;
  div {
    position: relative;
    flex: 1;
    padding: 5px 0;
    text-align: center;
    color: $blockActiveColor;
    &.active::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 0;
      border-bottom: 3px solid $blockActiveColor;
    }
    p {
      font-size: 12px;
      line-height: 20px;
    }
    .date {
      font-size: 16px;
      vertical-align: top;
    }
  }
}
.calendar-block {
  padding: 10px 0;
  margin-bottom: 20px;
  background-color: #fff;
  h3.title {
    margin-bottom: 10px;
    padding-left: 5px;
    line-height: 1;
    font-size: 16px;
    color: $blockActiveColor;
    border-left: 8px solid $blockActiveColor;
  }
  .finance-data-item {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    margin-left: 56px;
    margin-right: 10px;
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      margin-right: 10px;
      width: 100%;
      height: 0;
      border-bottom: 1px solid #eaeaea;
    }
    &:last-child::after {
      border-bottom: 0;
    }
    .region-pic {
      position: absolute;
      top: 10px;
      left: -46px;
      width: 36px;
      height: 36px;
    }
    .finance-influence {
      div {
        margin-left: 20px;
        width: 105px;
        line-height: 30px;
        font-size: 14px;
        border-radius: 4px;
        text-align: center;
      }
      .text-rise {
        color: #ff3232;
        border: 1px solid #ff3232;
        margin-bottom: 10px;
      }
      .text-drop {
        color: #00b200;
        border: 1px solid #00b200;
      }
    }
    .finance-detail {
      flex: 1;
    }
    .finance-detail-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
      line-height: 1;
      &:first-child {
        font-size: 14px;
      }
      &.forecast {
        font-size: 12px;
        color: #999;
      }
      .time {
        padding-left: 20px;
        color: $blockActiveColor;
        background: url(./assets/clock.png) no-repeat left center;
      }
      .state {
        color: #999;
        line-height: 18px;
        padding: 0 5px;
        border: 1px solid #999;
        border-radius: 4px;
      }
      .level {
        display: flex;
        width: 70px;
        height: 14px;
        background: url(./assets/star-bg.png) repeat-x center bottom;
        i {
          width: 14px;
          height: 14px;
          background: url(./assets/star.png) no-repeat center;
        }
      }
    }
  }
  .finance-tips {
    padding: 20px 0;
    text-align: center;
    color: #a4cce7;
    img {
      width: 200px;
      margin-bottom: 10px;
    }
  }
}
</style>


