<template>
  <div class="data-center-container animated fadeIn">
    <CalendarHeader :activeDate="new Date()" :showFilters="false"></CalendarHeader>
      <div class="data-center">
        <div class="hot-tags line-scroll">
          <router-link to="">非农数据</router-link>
          <router-link to="">EIA原油库存</router-link>
          <router-link to="">白银ETF</router-link>
          <router-link to="">黄金ETF</router-link>
          <router-link to="">API原油库存</router-link>
        </div>
        <div class="data-center-wrap">
          <div class="data-nav">
              <a 
                v-for="(item, index) in regionList"
                :data-id="index"
                :key="index"
                :class="{active: index == curIndex}"
                @click="changeIndex(index, item)">{{item}}</a>
          </div>
          <div class="data-list column-scroll">
            <mt-cell
              class="data-item"
              v-for="(item, index) in indexList"
              :key="index"
              :title="item.title"
              :to="{name: 'dataIndex', params: {id: item.id}}"
              is-link>
            </mt-cell>
            <div class="loading-btn">
              <mt-spinner v-show="isLoading" type="triple-bounce" color="#df5000" :size="40"></mt-spinner>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>

import CalendarHeader from './components/CalendarHeader.vue'

export default {
  data () {
    return {
      indexList: [],
      regionList: ['美国', '欧元区', '中国', '英国', '澳大利亚', '德国', '日本'],
      curIndex: 0,
      curRegion: '美国',
      isLoading: false,
    }
  },

  methods: {
    changeIndex(index, region){
      if(this.curIndex == index){
        return ;
      }
      this.curIndex = index;
      this.curRegion = region;
      this.getIndexInfo();
    },
    getIndexInfo(){
      if(this.isLoading){
        return ;
      }
      this.indexList.length = 0;
      this.isLoading = true;

      this.sendReq('I0037', {
        Region: encodeURI(this.curRegion)
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          this.isLoading = false;
          res.data.data.forEach((item, index) => {
            this.indexList.push({
              id: item.Id,
              title: item.IndexName
            })
          })
          // console.log(this.indexList);
        }
      })
      .catch((err) => { console.log(err); });
    }
  },

  mounted(){
    this.getIndexInfo();
  },

  created(){
    
  },

  components: {
    CalendarHeader
  }
}
</script>

<style lang="scss">
@import "./style/mixin";
.data-center-container {
  height: 100%;
}
.hot-tags {
  position: fixed;
  top: 50px;
  width: 100%;
  padding: 10px 0;
  background-color: #eef6fe;
  font-size: 0;
  a {
    box-sizing: border-box;
    display: inline-block;
    height: 30px;
    line-height: 28px;
    margin-left: 10px;
    padding: 0 10px;
    font-size: 14px;
    color: #666;
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 15px;
  }
}
.data-center {
  position: fixed;
  top: 100px;
  bottom: 55px;
  width: 100%;
  .data-center-wrap {
    height: 100%;
    background-color: #f2f5f8;
  }
  .data-nav {
    position: absolute;
    top: 0;
    left: 0;
    width: 130px;
    height: 100%;
    a {
      position: relative;
      box-sizing: border-box;
      display: block;
      width: 100%;
      height: 60px;
      line-height: 59px;
      text-align: center;
      color: #333;
      background-color: #f2f5f8;
      border-bottom: 1px solid #fff;
      &.active {
        background-color: #fff;
        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 0;
          height: 100%;
          border-left: 3px solid #26a2ff;
        }
      }
    }
  }
  .data-list {
    position: relative;
    height: 100%;
    margin-left: 130px;
    .data-item {
      background-color: #f7f7f7;
    }
    .mint-cell-wrapper {
      padding: 0 20px;
    }
    .mint-cell-value.is-link {
      margin-right: 0;
    }
    .mint-cell-title {
      box-sizing: border-box;
      width: 100%;
      height: 60px;
      padding-right: 10px;
      text-overflow: ellipsis;
      overflow: hidden;
      border-bottom: 1px solid #eaeaea;
    }
    .mint-cell-text {
      line-height: 59px;
    }
  }
}
.loading-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -10px;
  margin-left: -25px;
}
</style>


