<template>
  <div class="highcharts-container"></div>
</template>

<script>
import Highcharts from 'highcharts/highstock'

export default {
  name: 'highcharts',
  props: ['options'],
  data() {
    return {
      chart: null,
    }
  },
  mounted() {

  },
  methods: {
    initChart() {
      this.chart = new Highcharts.Chart(this.$el, this.options);
    }
  },
  created() {

  }
}
</script>

<style lang="scss">
  .highcharts-container {
    width: 100%;
    height: 200px;
  }
</style>


