              v15.212c0,3.502,2.334,5.874,5.874,5.874c3.502,0,5.874-2.334,5.874-5.874V22.29h87.128v15.212c0,3.502,2.334,5.874,5.874,5.874
              c3.537,0,5.873-2.334,5.873-5.874V22.29h21.838c9.375,0,14.646,7.041,14.646,16.417v9.948H12.849v-9.948
              C12.849,29.219,22.036,22.29,31.411,22.29L31.411,22.29z M183.148,188.006H31.675c-9.376,0-18.939-7.041-18.939-16.417V60.328
              h187.732v111.299C200.506,180.701,192.523,188.006,183.148,188.006L183.148,188.006z M183.148,188.006"/>
        </svg>
      </aside>
      <div class="calendar-nav">
        <router-link to="/calendar" class="nav-l" :class="{active: this.$route.name == 'rili'}">日历</router-link>
        <router-link to="/data" class="nav-r" :class="{active: this.$route.name == 'data'}">数据</router-link>
      </div>
      <aside class="icon icon-r" v-if="showFilters" @click="switchFilter">
        <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" viewBox="0 0 213.672 200" fill="#fff">
          <path d="M202.696,31.617l0.09,0.077c0,0-40.831,39.835-58.658,57.061c-1.262,1.217-3.736,3.456-4.417,5.049
            c-0.714,1.663-0.627,5.228-0.627,7.026c0,32.945,0.454,92.22,0.454,92.22s0.122,6.946-7.156,6.95
            c-5.641,0.004-5.917-5.348-5.917-6.95c0-45.117-0.021-69.302-0.028-94.076c-0.004-6.583-0.526-7.871,0.215-10.264
