            c-3.663-3.345-5.965-8.038-5.965-13.28C6.245,8.207,14.741,0,25.226,0h164.456c10.487,0,18.979,8.207,18.979,18.338
            C208.661,23.579,206.357,28.271,202.696,31.617L202.696,31.617z M202.696,31.617"/>
        </svg>
      </aside>
    </header>
    <mt-popup
      v-model="showFilter"
      class="calendar-filter"
      position="top">
        <div class="calendar-filter-item">
          <p class="title">国家地区</p>
          <div class="filter-list">
            <mt-button v-for="item in rigionArr" :key="item" :class="[item == curRion ? 'mint-button--primary' : 'mint-button--default', item == curRion ? '' : 'is-plain']" @click="changeRion(item)">{{item}}</mt-button>
          </div>
        </div>
        <div class="calendar-filter-item">
          <p class="title">重要性</p>
          <div class="filter-list">
            <mt-button v-for="item in importArr" :key="item" :class="[item == curImport ? 'mint-button--primary' : 'mint-button--default', item == curImport ? '' : 'is-plain']" @click="changeImport(item)">{{item}}</mt-button>
          </div>
        </div>
    </mt-popup>
    <mt-popup
      v-model="showCalendar"
      class="calendar-filter"
      position="top">
        <div class="calendar-header">
          <span class="l-icon" @click="toPrev">
            <svg class="icon" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" width="100%" height="100%">
              <path d="M741.87776 1023.96928c-16.0512 0-32.1024-5.98016-44.34944-17.98144L237.824 555.4688a60.53888 60.53888 0 0 1 0-86.87616l459.70944-450.56c24.48896-24.00256 64.15872-24.00256 88.64768 0a60.54912 60.54912 0 0 1 0 86.87616l-415.36 407.10144 415.36 407.06048a60.55424 60.55424 0 0 1 0 86.88128c-12.20096 12.03712-28.25216 18.01728-44.30336 18.01728z" p-id="5737"></path>
            </svg>
          </span>
          <p>{{formatDate | monthFormat}}</p>
          <span class="r-icon" @click="toNext">
            <svg class="icon" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" width="100%" height="100%">
              <path d="M741.87776 1023.96928c-16.0512 0-32.1024-5.98016-44.34944-17.98144L237.824 555.4688a60.53888 60.53888 0 0 1 0-86.87616l459.70944-450.56c24.48896-24.00256 64.15872-24.00256 88.64768 0a60.54912 60.54912 0 0 1 0 86.87616l-415.36 407.10144 415.36 407.06048a60.55424 60.55424 0 0 1 0 86.88128c-12.20096 12.03712-28.25216 18.01728-44.30336 18.01728z" p-id="5737"></path>
            </svg>
          </span>
        </div>
        <div class="calendar-table">
          <div class="calendar-thead">
            <span>周日</span>
            <span>周一</span>
            <span>周二</span>
            <span>周三</span>
            <span>周四</span>
            <span>周五</span>
            <span>周六</span>
          </div>
          <mt-swipe ref="swipe" class="calendar-tbody" :defaultIndex="1" :show-indicators="false" :auto="0" @change="handleChange">
            <mt-swipe-item>
              <span v-for="(n, i) in month1.list" :class="[i < month1.prev ? 'last' : '', i < month1.prev + month1.len ? '' : 'next']" @click="changeDate(month1, i, n)">{{n}}</span>
            </mt-swipe-item>
            <mt-swipe-item>
              <span v-for="(n, i) in month2.list" :class="[i < month2.prev ? 'last' : '', i < month2.prev + month2.len ? '' : 'next', month2.active && curDay.getDate() == i - month2.prev + 1 ? 'active' : '']" @click="changeDate(month2, i, n)">{{n}}</span>
            </mt-swipe-item>
            <mt-swipe-item>
              <span v-for="(n, i) in month3.list" :class="[i < month3.prev ? 'last' : '', i < month3.prev + month3.len ? '' : 'next']" @click="changeDate(month3, i, n)">{{n}}</span>
            </mt-swipe-item>
          </mt-swipe>
        </div>
    </mt-popup>
  </div>
</template>
<script>

export default {
  props: ['activeDate', 'showFilters', 'curRion', 'curImport'],
  data () {
    return {
      showCalendar: false,
      showFilter: false,
      rigionArr: ['全部', '欧元区', '德国', '英国', '法国', '西班牙', '意大利', '瑞士', '美国', '加拿大', '中国', '日本', '韩国', '香港', '台湾', '新加坡', '澳大利亚', '新西兰'],
      importArr: ['全部', '高', '中', '低'],
      nowIndex: 1,
      curDay: new Date(),
      month1: {
        prev: 0,
        next: 0,
        list: [],
        len: 0
      },
      month2: {
        prev: 0,
        next: 0,
        list: [],
        len: 0
      },
      month3: {
        prev: 0,
        next: 0,
        list: [],
        len: 0
      },
      activeYear: new Date().getFullYear(),
      activeMonth: new Date().getMonth()
    }
  },

  computed: {
    formatDate(){
      return new Date(this.activeYear, this.activeMonth, 1);
    }
  },

  methods: {
    handleChange(index){
      this.nowIndex = index;
    },
    switchFilter(){
      if(this.showCalendar){
        this.switchCalendar();
      }
      this.showFilter = !this.showFilter;
    },
    switchCalendar(){
      if(this.showFilter){
        this.switchFilter();
      }
      this.activeYear = new Date().getFullYear();
      this.activeMonth = new Date().getMonth();
      this.setMonth();
      this.showCalendar = !this.showCalendar;
    },
    changeRion(o){
      this.$emit('setRion', o);
      this.showFilter = false;
    },
    changeImport(o){
      this.$emit('setImport', o);
      this.showFilter = false;
    },
    toPrev(){
      this.$refs.swipe.prev();
    },
    toNext(){
      this.$refs.swipe.next();
    },
    initCalendar(){
      var activeYear = this.activeYear;
      var activeMonth = this.activeMonth;

      var curMonth = {list: []}, prevMonth = {list: []}, nextMonth = {list: []};

      var lastDay0 = new Date(activeYear, activeMonth - 1, 1).getDay();
      var lastDay1 = new Date(activeYear, activeMonth, 1).getDay();
      var lastDay2 = new Date(activeYear, activeMonth + 1, 1).getDay();

      var beforePrevMonthL = new Date(activeYear, activeMonth - 1, 0).getDate();
      var prevMonthL = new Date(activeYear, activeMonth, 0).getDate();
      var activeMonthL = new Date(activeYear, activeMonth + 1, 0).getDate();
      var nextMonthL = new Date(activeYear, activeMonth + 2, 0).getDate();

      prevMonth.prevNum = lastDay0 == 0 ? 7 : lastDay0;
      curMonth.prevNum = lastDay1 == 0 ? 7 : lastDay1;
      nextMonth.prevNum = lastDay2 == 0 ? 7 : lastDay2;

      prevMonth.nextNum = 42 - prevMonth.prevNum - prevMonthL;
      curMonth.nextNum = 42 - curMonth.prevNum - activeMonthL;
      nextMonth.nextNum = 42 - nextMonth.prevNum - nextMonthL;

      prevMonth.len = prevMonthL;
      curMonth.len = activeMonthL;
      nextMonth.len = nextMonthL;


      for (var i = 1; i <= activeMonthL; i++) {
        curMonth.list.push(i);
      }
      for (var i = 0; i < curMonth.prevNum; i++) {
        curMonth.list.unshift(prevMonthL - i);
      }
      for (var i = 1; i <= curMonth.nextNum; i++) {
        curMonth.list.push(i);
      }

      for (var i = 1; i <= prevMonthL; i++) {
        prevMonth.list.push(i);
      }
      for (var i = 0; i < prevMonth.prevNum; i++) {
        prevMonth.list.unshift(beforePrevMonthL - i);
      }
      for (var i = 1; i <= prevMonth.nextNum; i++) {
        prevMonth.list.push(i);
      }

      for (var i = 1; i <= nextMonthL; i++) {
        nextMonth.list.push(i);
      }
      for (var i = 0; i < nextMonth.prevNum; i++) {
        nextMonth.list.unshift(activeMonthL - i);
      }
      for (var i = 1; i <= nextMonth.nextNum; i++) {
        nextMonth.list.push(i);
      }

      return {
        curMonth,
        prevMonth,
        nextMonth
      };
    },
    setMonth(){
      var monthObj = this.initCalendar();
      this.month1.list.length = 0;
      this.month2.list.length = 0;
      this.month3.list.length = 0;
      if(this.nowIndex == 0){
        this.month1.prev = monthObj.curMonth.prevNum;
        this.month1.next = monthObj.curMonth.nextNum;
        this.month1.len = monthObj.curMonth.len;
        monthObj.curMonth.list.forEach((o, i) => {
          this.month1.list.push(o);
        })
        this.month2.prev = monthObj.nextMonth.prevNum;
        this.month2.next = monthObj.nextMonth.nextNum;
        this.month2.len = monthObj.nextMonth.len;
        monthObj.nextMonth.list.forEach((o, i) => {
          this.month2.list.push(o);
        })
        this.month3.prev = monthObj.prevMonth.prevNum;
        this.month3.next = monthObj.prevMonth.nextNum;
        this.month3.len = monthObj.prevMonth.len;
        monthObj.prevMonth.list.forEach((o, i) => {
          this.month3.list.push(o);
        })
      } else if(this.nowIndex == 1){
        this.month1.prev = monthObj.prevMonth.prevNum;
        this.month1.next = monthObj.prevMonth.nextNum;
        this.month1.len = monthObj.prevMonth.len;
        monthObj.prevMonth.list.forEach((o, i) => {
          this.month1.list.push(o);
        })
        this.month2.prev = monthObj.curMonth.prevNum;
        this.month2.next = monthObj.curMonth.nextNum;
        this.month2.len = monthObj.curMonth.len;
        monthObj.curMonth.list.forEach((o, i) => {
          this.month2.list.push(o);
        })
        this.month3.prev = monthObj.nextMonth.prevNum;
        this.month3.next = monthObj.nextMonth.nextNum;
        this.month3.len = monthObj.nextMonth.len;
        monthObj.nextMonth.list.forEach((o, i) => {
          this.month3.list.push(o);
        })
      } else {
        this.month1.prev = monthObj.nextMonth.prevNum;
        this.month1.next = monthObj.nextMonth.nextNum;
        this.month1.len = monthObj.nextMonth.len;
        monthObj.nextMonth.list.forEach((o, i) => {
          this.month1.list.push(o);
        })
        this.month2.prev = monthObj.prevMonth.prevNum;
        this.month2.next = monthObj.prevMonth.nextNum;
        this.month2.len = monthObj.prevMonth.len;
        monthObj.prevMonth.list.forEach((o, i) => {
          this.month2.list.push(o);
        })
        this.month3.prev = monthObj.curMonth.prevNum;
        this.month3.next = monthObj.curMonth.nextNum;
        this.month3.len = monthObj.curMonth.len;
        monthObj.curMonth.list.forEach((o, i) => {
          this.month3.list.push(o);
        })
      }
    },
    changeDate(o, i, n){
      if(i < o.prev){
        this.toPrev();
      } else if(i >= o.prev + o.len){
        this.toNext();
      } else {
        this.showCalendar = false;
        this.$emit('resetDate', new Date(this.activeYear, this.activeMonth, n));
      }
    }
  },

  filters: {
    monthFormat(value){
      var year = value.getFullYear();
      var month = (value.getMonth() + 1) < 10 ? '0' + (value.getMonth() + 1) : (value.getMonth() + 1);
      return year + '年' + month + '月';
    }
  },

  mounted(){

  },

  watch: {
    nowIndex(oldV, newV){
      if(oldV < newV){
        if(oldV == 0 && newV == 2){
          console.log('向右');
          this.activeMonth += 1;
          this.setMonth();
          return ;
        }
        console.log('向左');
        this.activeMonth -= 1;
        // this.activeDate = new Date(this.activeYear, this.activeMonth - 1, 1);
        this.setMonth();
      } else if(oldV > newV){
        if(oldV == 2 && newV == 0){
          console.log('向左');
          this.activeMonth -= 1;
          // this.activeDate = new Date(this.activeYear, this.activeMonth - 1, 1);
          this.setMonth();
          return ;
        }
        console.log('向右');
        this.activeMonth += 1;
        // this.activeDate = new Date(this.activeYear, this.activeMonth + 1, 1);
        this.setMonth();
      }
    }
  },

  created(){
    
  }
}
</script>

<style lang="scss">
@import "../style/mixin";
.calendar-nav {
  width: 160px;
  margin: 0 auto;
  padding: 10px 0;
  font-size: 0;
  a {
    box-sizing: border-box;
    display: inline-block;
    width: 80px;
    height: 30px;
    font-size: 16px;
    line-height: 28px;
    text-align: center;
    border: 1px solid #fff;
    &.nav-l {
      border-right: 0;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }
    &.nav-r {
      border-left: 0;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }
    &.active {
      color: #26a2ff;
      background-color: #fff;
      background-color: rgba(255, 255, 255, 0.7);
    }
  }
}
.calendar-filter {
  top: 50px;
  width: 100%;
  z-index: 51 !important;
  .calendar-filter-item {
    padding: 10px;
    overflow: hidden;
    border-bottom: 1px solid #eaeaea;
    &:last-child {
      border-bottom: none;
    }
    .title {
      margin-bottom: 10px;
    }
    .filter-list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 0 10px;
      button {
        width: 88px;
        height: 36px;
        padding: 0;
        margin-bottom: 10px;
        font-size: 16px;
      }
    }
  }
}
.rili-tabbar {
  .v-modal {
    z-index: 50 !important;
  }
}
.calendar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  line-height: 20px;
  padding: 0 25%;
  background-color: #eef6fe;
  color: $blockActiveColor;
  span {
    width: 16px;
    height: 16px;
  }
  .icon {
    fill: $blockActiveColor;
  }
  .r-icon {
    transform: rotate(180deg);
  }
}
.calendar-table {
  font-size: 14px;
  line-height: 40px;
  span {
    float: left;
    width: 14.28571%;
    text-align: center;
    &.last, &.next {
      color: #c1c1c1;
    }
    &.active {
      color: #fff;
      background-color: $blockActiveColor;
    }
  }
}
.calendar-thead {
  color: $blockActiveColor;
}
.calendar-tbody {
  width: 100%;
  height: 240px;
}
</style>


