<template>
  <div class="data-detail block animated fadeIn">
    <page-head linkUrl="/calendar" :pageTitle="indexInfo.title"></page-head>
    <div class="data-chart">
      <p class="data-title">{{indexInfo.title}}</p>
      <data-chart ref="chart" :options="options"></data-chart>
    </div>
    <div class="data-block">
      <h3>基本信息</h3>
      <p>数据公布机构： {{indexInfo.unit}}</p>
      <p>发布频率： {{indexInfo.cycle}}</p>
    </div>
    <div class="data-block">
      <h3>数据影响</h3>
      <p>数据影响：{{indexInfo.influence}}</p>
    </div>
    <div class="data-block">
      <h3>数据释义</h3>
      <p>{{indexInfo.explain}}</p>
    </div>
    <div class="data-block">
      <h3>统计方法</h3>
      <p>统计方法：{{indexInfo.methods || '--'}}</p>
    </div>
    <div class="data-block">
      <h3>关注原因</h3>
      <p>{{indexInfo.reason || '--'}}</p>
    </div>
    <div class="data-block">
      <h3>历史数据<span class="title-tips">（单位：万桶）</span></h3>
      <table class="history-data">
        <thead>
          <tr>
              <th>公布日期</th>
              <th>前值</th>
              <th>预测值</th>
              <th>公布值</th>
              <th>影响</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in historyData">
            <td>{{item.time | dateFormat}}</td>
            <td>{{item.oldVal}}</td>
            <td>{{item.forecastVal}}</td>
            <td>{{item.publishVal}}</td>
            <td :class="item.influenceClass">{{item.influenceText}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>

import PageHead from './components/PageHead.vue';
import DataChart from './components/DataChart.vue';

export default {
  data () {
    return {
      indexInfo: {
        title: '',
        explain: '',
        cycle: '',
        unit: '',
        influence: '',
        methods: '',
        reason: ''
      },
      id: '',
      historyData: [],
      options: {
        title: {
          text: ''
        },
        tooltip: {
          headerFormat: '',
          pointFormatter: function() {
              return '<span style="">2017年03月01日</span><br/>公布值：' + this.y + '万桶.'
          }
        },
        xAxis: {
          type: 'datetime',
          dateTimeLabelFormats: {
            second: '%Y-%m-%d',
            minute: '%Y-%m-%d',
            hour: '%Y-%m-%d',
            day: '%Y-%m-%d',
            month: '%Y-%m',
            year: '%Y'
          }
        },
        yAxis: {
            title: {
                text: ''
            },
            labels: {
              formatter:function (){   
                return this.value + '万桶' ;   
              }
            }
            // lineWidth: 2,
            // lineColor: '#000',
            // id: 'temperature-axis'
        },
        series: [{
            name: 'Temperature',
            data: [],
            color: '#7cb5ec'
        }],
        credits: {
          enabled: false
        },
        legend: {
          enabled: false
        }
      }
    }
  },

  methods: {
    getIndexInfo(){
      this.sendReq('I0038', {
        IndexId: this.id
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          let resInfo = res.data.data[0];
          // console.log(resInfo);
          this.indexInfo = {
            title: resInfo.IndexName,
            explain: resInfo.Explain,
            cycle: resInfo.Frequency,
            unit: resInfo.PublishOrgan,
            influence: resInfo.Influence,
            methods: resInfo.StatisticMethod
          };
        }
      })
      .catch((err) => { console.log(err); });
    },
    getHistoryData(){
      this.sendReq('I0039', {
        IndexId: this.id
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          res.data.data.forEach((item, index) => {
            var text = (item.Benefit == null) ? '影响较小' : ((item.Benefit.indexOf('美元') == -1) ? item.DeBenefit : item.Benefit);
            var str = text.substring(0, 2);
            var level;
            if(str == '利空'){
              level = 'text-drop';
            } else if(str == '利多'){
              level = 'text-rise';
            } else {
              level = 'text-normal';
            }
            this.historyData.push({
              time: new Date(parseInt(item.PublishDate.match(/\d+/)[0])),
              oldVal: item.PreviousValue,
              publishVal: item.PublishValue,
              forecastVal: item.ForecastValue,
              influenceText: text,
              influenceClass: level
            });
            this.options.series[0].data.unshift([parseInt(item.PublishDate.match(/\d+/)[0]), parseFloat(item.PublishValue)]);
          })
          this.$refs.chart.initChart();
        }
      })
      .catch((err) => { console.log(err); });
    }
  },

  created(){
    this.id = this.$route.params.id;
    this.getIndexInfo();
    this.getHistoryData();
  },

  mounted() {

  },

  components: {
    PageHead,
    DataChart
  }
}
</script>

<style lang="scss">
  @import "./style/mixin";
  .data-chart {
    .data-title {
      text-align: center;
      font-size: 18px;
      padding: 10px 0;
    }
  }
  .data-block {
    padding: 10px;
    border-bottom: 1px solid #eaeaea;
    h3 {
      font-size: 16px;
      margin-bottom: 10px;
    }
    .title-tips {
      font-size: 14px;
      font-weight: normal;
      color: #666;
    }
    .history-data {
      width: 100%;
      border-collapse: collapse;
      th {
        color: #fff;
        background-color: $blockActiveColor;
        border-right: 1px solid #fff;
        &:last-child {
          border-right: none;
        }
      }
      td {
        border: 1px solid #eaeaea;
        &.text-drop {
          color: #4caf50;
        }
        &.text-rise {
          color: #f44336;
        }
        &.text-normal {
          color: #ffc107;
        }
      }
      th, td {
        box-sizing: border-box;
        height: 30px;
        text-align: center;
      }
    }
  }
</style>


