<template>
  <div class="highcharts-container"></div>
</template>

<script>
import Highcharts from 'highcharts/highstock'

export default {
  name: 'highcharts',
  data() {
    return {
      chart: null,
      options: {
        title: {
          text: '月平均气温'
        },
        tooltip: {
          headerFormat: '',
          pointFormatter: function() {
              return '<span style="">2017年03月01日</span><br/>公布值：' + this.y + '万桶.'
          }
        },
        xAxis: {
          type: 'datetime',
          dateTimeLabelFormats: {
            second: '%Y-%m-%d',
            minute: '%Y-%m-%d',
            hour: '%Y-%m-%d',
            day: '%Y-%m-%d',
            month: '%Y-%m',
            year: '%Y'
          }
        },
        yAxis: {
            title: {
                text: ''
            },
            labels: {
              formatter:function (){   
                return this.value + '万桶' ;   
              }
            }
            // lineWidth: 2,
            // lineColor: '#000',
            // id: 'temperature-axis'
        },
        series: [{
            name: 'Temperature',
            data: [],
            color: '#7cb5ec'
        }],
        credits: {
          enabled: false
        },
        legend: {
          enabled: false
        }
      },
      styles: {
        width: 400,
        height: 300
      }
    }
  },
  mounted() {
    this.getHistoryData();
  },
  methods: {
    initChart() {
      this.$el.style.width = (this.styles.width || 800) + 'px';
      this.$el.style.height = (this.styles.height || 400) + 'px';
      this.chart = new Highcharts.Chart(this.$el, this.options);
    },
    getHistoryData(){
      this.sendReq('I0039', {
        IndexId: '40afadc7-42cc-4083-8e15-9c6775e5aa90'
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          res.data.data.forEach((item, index) => {
            this.options.series[0].data.unshift([parseInt(item.PublishDate.match(/\d+/)[0]), parseFloat(item.PublishValue)]);
          })
        }
        this.initChart();
      })
      .catch((err) => { console.log(err); });
    }
  },
  created() {
    // this.getHistoryData();
  }
}
</script>

<style lang="scss">
  .highcharts-container {
    width: 800px;
    height: 600px;
  }
</style>


