<template>
  <div class="search-container animated fadeIn">
    <div class="recommend-product">
      <div class="title">大家都在看的</div>
      <div class="product-list">
        <router-link to="/quotes/DXY">
          <mt-button type="primary" size="small">美元指数</mt-button>
        </router-link>
        <router-link to="/quotes/XAUUSD">
          <mt-button type="primary" size="small">黄金</mt-button>
        </router-link>
        <router-link to="/quotes/HKG50">
          <mt-button type="primary" size="small">恒生指数</mt-button>
        </router-link>
      </div>
    </div>
    <div class="search-form">
      <mt-search v-model="value" :autofocus="true" placeholder="请输入关键字">
        <mt-cell
          v-for="(item, index) in result"
          :title="item.name"
          :value="item.code"
          :key="index"
          :to="{name: 'product', params: {name: item.code}}">
        </mt-cell>
      </mt-search>
      <router-link to="/markets" class="icon icon-l">
        <svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" fill="#5a5a5a" width="100%" height="100%">
          <path d="M741.87776 1023.96928c-16.0512 0-32.1024-5.98016-44.34944-17.98144L237.824 555.4688a60.53888 60.53888 0 0 1 0-86.87616l459.70944-450.56c24.48896-24.00256 64.15872-24.00256 88.64768 0a60.54912 60.54912 0 0 1 0 86.87616l-415.36 407.10144 415.36 407.06048a60.55424 60.55424 0 0 1 0 86.88128c-12.20096 12.03712-28.25216 18.01728-44.30336 18.01728z" p-id="5737"></path>
        </svg>
      </router-link>
      <mt-button type="default" plain>搜索</mt-button>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      value: '',
      result: []
    }
  },
  watch: {
    value(val){
      if(val.length == 0){
        return ;
      }
      this.result.length = 0;
      this.sendReq('I0036', {
        keyWord: val
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          if(res.data.data == null){
            return ;
          }
          res.data.data.forEach((item, index) => {
            if(this.result.length > 5){
              return ;
            }
            this.result.push({name: item.ProductName, code: item.Code})
          })
        }
      })
      .catch((err) => { console.log(err); });
    }
  },
  mounted() {

  },
  methods: {

  },
  created() {
  }
}
</script>

<style lang="scss">
  .search-container {
    background-color: #fff;
    .mint-searchbar-cancel {
      display: none !important;
    }
    .search-form {
      position: relative;
      .mint-button {
        position: absolute;
        top: 10px;
        right: 5px;
        border: 0;
        z-index: 5;
        height: 36px;
        &:active::after {
          opacity: 0;
        }
      }
      .icon-l {
        position: absolute;
        top: 18px;
        left: 10px;
        width: 20px;
        height: 20px;
        z-index: 5;
      }
    }
    .mint-search-list {
      z-index: 2;
      margin-top: 56px;
      padding-top: 0;
      background-color: #fff;
      .mint-cell {
        border-bottom: 1px solid #eaeaea;
      }
    }
    .mint-searchbar-inner {
      background-color: #f0f2f5;
      .mint-searchbar-core {
        padding-left: 5px;
        background-color: #f0f2f5;
      }
      .mintui-search {
        font-size: 18px;
      }
    }
    .mint-searchbar {
      background-color: transparent;
      padding: 10px 72px 10px 45px;
    }
    .recommend-product {
      position: absolute;
      top: 70px;
      left: 0;
      width: 100%;
      z-index: 1;
      .title {
        line-height: 2;
        padding-left: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid #eaeaea;
      }
      .mint-button {
        margin-left: 15px;
        background-color: #f1f2f6;
        border-radius: 4px;
        color: #333;
      }
    }
  }
</style>


