<template>
  <div class="markets-container animated fadeIn">
    <div class="zdcj-header markets-head">
      行情中心
      <router-link class="icon icon-r" to="/search">
        <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" viewBox="0 0 200 200" fill="#fff">
        <path d="M196.221,186.899l-44.175-46.351c12.777-15.374,19.804-34.537,19.804-54.71C171.85,38.512,133.303,0,85.918,0
          C38.547,0,0,38.505,0,85.838s38.547,85.837,85.925,85.837c12.413-0.005,24.382-2.595,35.572-7.686
          c3.325-1.512,4.792-5.428,3.279-8.742c-1.515-3.329-5.453-4.803-8.751-3.277c-9.473,4.307-19.598,6.493-30.107,6.5
          c-40.087,0-72.698-32.582-72.698-72.632c0-40.043,32.611-72.631,72.698-72.631c40.093,0,72.711,32.581,72.711,72.631
          c0,18.853-7.198,36.715-20.258,50.299c-2.531,2.626-2.446,6.809,0.178,9.336c0.199,0.192,0.952,0.998,1.177,1.235l46.84,49.197
          c1.295,1.393,3.06,2.094,4.825,2.094c1.618,0,3.238-0.589,4.514-1.785C198.568,193.74,198.714,189.56,196.221,186.899
          L196.221,186.899z"/>
        </svg>
      </router-link>
    </div>
    <mt-navbar class="zdcj-nav" v-model="selected">
      <mt-tab-item v-for="(item, index) in cate" :id="index" :key="index">{{index}}</mt-tab-item>
    </mt-navbar>

    <mt-tab-container v-model="selected" :swipeable="true" class="column-scroll">
      <mt-tab-container-item v-for="(o, i) in cate" :id="i" :key="i">
        <table class="markets-list">
         <thead>
            <tr>
              <th>名称</th>
              <th>最新价</th>
              <th>涨跌幅</th>
            </tr>
         </thead>
         <tbody>
            <tr v-for="(item, index) in cate[i]" :key="index" @click="linkTo(item.code)">
              <td><p class="name">{{item.name}}</p><p class="code">{{item.code}}</p></td>
              <td><p class="price" :class="[item.price - item.openPrice >= 0 ? 'text-rise' : 'text-drop']">{{item.price}}</p></td>
              <td class="state"><p>{{index | percentFormat(item.price, item.openPrice) | numFormat(item.price, item.openPrice)}}</p><p>{{index | pointFormat(item.price, item.openPrice) | numFormat(item.price, item.openPrice)}}</p></td>
            </tr>
         </tbody>
        </table>
        <!-- <router-link class="markets-item" v-for="(item, index) in cate[i]" :key="index" :to="{name: 'product', params: {name: item.code}}">
          <div>{{item.name}}</div>
          <div class="price" :class="[item.price - item.openPrice >= 0 ? 'text-rise' : 'text-drop']">{{item.price}}</div>
          <div class="state"><span>{{index | pointFormat(item.price, item.openPrice) | numFormat(item.price, item.openPrice)}}</span><span>{{index | percentFormat(item.price, item.openPrice) | numFormat(item.price, item.openPrice)}}</span></div>
        </router-link> -->
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script>

export default {
  data () {
    return {
      selected: '所有',
      cate: {
        "所有": [],
        "外汇": [],
        "原油": [],
        "金银": [],
        "指数": []
      }
    }
  },

  methods: {
    getMarketsList(){
      this.sendReq('I0042', {
        TypeId: '', // 0:表示根据用户ID查，UserId不可空
        PageIndex: 1,
        PageSize: 100
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          res.data.data.forEach((item, index) => {
            this.cate['所有'].push({
              name: item.ProductName,
              code: item.Code,
              price: item.NowPrice,
              openPrice: item.OpenPrice
            });
            this.cate[item.SortName].push({
              name: item.ProductName,
              code: item.Code,
              price: item.NowPrice,
              openPrice: item.OpenPrice
            });
          });
        }
      })
      .catch((err) => { console.log(err); });
    },
    linkTo(code){
      this.$router.push({name: 'product', params: {name: code}});
    }
  },

  filters: {
    pointFormat(i, val, compareVal){
      return val.replace('.', '') - compareVal.replace('.', '');
    },
    percentFormat(i, val, compareVal){
      return ((val - compareVal) / compareVal * 100).toFixed(2) + '%';
    },
    numFormat(i, val, compareVal){
      return val - compareVal >= 0 ? ('+' + i) : i;
    }
  },

  created(){
    this.getMarketsList();
  }
}
</script>

<style lang="scss">
  .markets-head {
    text-align: center;
    line-height: 50px;
    font-size: 18px;
  }
  .markets-container {
    box-sizing: border-box;
    height: 100%;
    .mint-tab-container {
      position: fixed;
      width: 100%;
      top: 100px;
      bottom: 55px;
    }
  }
  .markets-list {
    width: 100%;
    border-collapse: collapse;
    th, td {
      padding: 0;
    }
    thead {
      th {
        height: 50px;
        background-color: #f8f8f8;
        font-weight: normal;
      }
    }
    tbody {
      td {
        padding: 20px 0;
        border-bottom: 1px solid #eaeaea;
        text-align: center;
        line-height: 20px;
        background-color: #fff;
        &.state {
          font-size: 14px;
        }
      }
    }
    .code, .time {
      font-size: 13px;
      color: #999;
    }
    .price {
      font-weight: bold;
      font-size: 16px;
      &::after {
        content: "";
        width: 0;
        height: 0;
        display: inline-block;
        margin-left: 3px;
        margin-bottom: 2px;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
      }
      &.text-rise {
        color: #ff3232;
        &::after {
          border-bottom: 7px solid #ff3232;
        }
      }
      &.text-drop {
        color: #00b200;
        &::after {
          border-top: 7px solid #00b200;
        }
      }
    }
  }
  .markets-item {
    float: left;
    box-sizing: border-box;
    width: 30%;
    margin-top: 2.5%;
    margin-left: 2.5%;
    padding: 10px;
    line-height: 1.5;
    background-color: #fff;
    text-align: center;
    font-size: 14px;
    color: #333;
    .price {
      font-weight: bold;
      font-size: 16px;
      &::after {
        content: "";
        width: 0;
        height: 0;
        display: inline-block;
        margin-left: 3px;
        margin-bottom: 2px;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
      }
      &.text-rise {
        color: #ff3232;
        &::after {
          border-bottom: 7px solid #ff3232;
        }
      }
      &.text-drop {
        color: #00b200;
        &::after {
          border-top: 7px solid #00b200;
        }
      }
    }
    .state {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
    }
  }
</style>


