<template>
  <div class="product-container animated fadeIn">
    <div class="zdcj-header product-head">
      <div>
        <p class="name">{{product.name}}</p>
        <p class="code">{{product.code}}</p>
      </div>
      <aside class="icon icon-l">
        <router-link to="/markets">
          <svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" fill="#fff" width="100%" height="100%">
            <path d="M741.87776 1023.96928c-16.0512 0-32.1024-5.98016-44.34944-17.98144L237.824 555.4688a60.53888 60.53888 0 0 1 0-86.87616l459.70944-450.56c24.48896-24.00256 64.15872-24.00256 88.64768 0a60.54912 60.54912 0 0 1 0 86.87616l-415.36 407.10144 415.36 407.06048a60.55424 60.55424 0 0 1 0 86.88128c-12.20096 12.03712-28.25216 18.01728-44.30336 18.01728z" p-id="5737"></path>
          </svg>
        </router-link>
      </aside>
      <aside class="icon icon-r">
        <mt-button class="select" size="small" plain>+自选</mt-button>
      </aside>
    </div>
    <div class="product-data-container">
      <div class="product-data-row">
        <div class="price">{{price.now}}</div>
        <div class="last-data">
          <span>-13.82</span><span>-0.42%</span>
          <p>最后更新时间：15:00:06</p>
        </div>
      </div>
      <div class="product-data-row">
        <div class="product-data-item">
          <p>今开</p>
          <p>{{price.open}}</p>
        </div>
        <div class="product-data-item">
          <p>昨收</p>
          <p>{{price.close}}</p>
        </div>
        <div class="product-data-item">
          <p>最高</p>
          <p>{{price.highest}}</p>
        </div>
        <div class="product-data-item">
          <p>最低</p>
          <p>{{price.lowest}}</p>
        </div>
      </div>
    </div>
    <div class="product-chart">
      <mt-navbar class="zdcj-nav chart-navbar" v-model="selected">
        <mt-tab-item v-for="(item, index) in options.rangeSelector.buttons" v-show="index != 5" :id="index" :key="index" @click.native="getChartData">{{item.text}}</mt-tab-item>
      </mt-navbar>
      <div class="chart" ref="chart">
        
      </div>
    </div>
  </div>
</template>

<script>

import Highcharts from 'highcharts/highstock'

export default {
  data() {
    return {
      product: {
        name: '',
        code: ''
      },
      price: {
        open: '',
        close: '',
        now: '',
        highest: '',
        lowest: ''
      },
      selected: 2,
      // cate: ['1分', '5分', '15分', '30分', '1小时', '4小时', '日线'],
      // chart: null,
      options: {
        rangeSelector : {
          buttons: [{
              type: 'hour',
              count: 2,
              text: '1分'
          }, {
              type: 'minute',
              count: 60,
              text: '5分'
          }, {
              type: 'hour',
              count: 4,
              text: '15分'
          }, {
              type: 'minute',
              count: 30,
              text: '30分'
          }, {
              type: 'hour',
              count: 5,
              text: '1小时'
          }, {
              type: 'day',
              count: 2,
              text: '2小时'
          }, {
              type: 'day',
              count: 4,
              text: '4小时'
          }, {
              type: 'day',
              count: 30,
              text: '日线'
          }],
          buttonTheme: {
            display: 'none'
          },
          inputEnabled: false,
          selected : 4
        },
        title : {
            text : ''
        },
        xAxis: {
          dateTimeLabelFormats: {
            millisecond: '%H:%M:%S.%L',
            second: '%H:%M:%S',
            minute: '%H:%M',
            hour: '%H:%M',
            day: '%m-%d',
            week: '%m-%d',
            month: '%y-%m',
            year: '%Y'
          }
        },
        yAxis: {
            title: {
                text: ''
            },
            labels: {
              // formatter:function (){   
              //   return this.value;   
              // }
            }
            // lineWidth: 2,
            // lineColor: '#000',
            // id: 'temperature-axis'
        },
        series : [{
            type : 'candlestick',
            name : 'AAPL Stock Price',
            data : [],
            color: 'green',
            lineColor: 'green',
            upColor: 'red',
            upLineColor: 'red',
            // dataGrouping : {
            //     units : [
            //         [
            //             'week', 
            //             [1] 
            //         ], [
            //             'month',
            //             [1, 2, 3, 4, 6]
            //         ]
            //     ]
            // }
        }],
        credits: {
          enabled: false
        },
        legend: {
          enabled: false
        },
        navigator: {
          xAxis: {
            labels: {
              formatter: function() {
                return Highcharts.dateFormat('%m-%d', this.value);
              }
            }
          }
        }
      }
    }
  },
  mounted() {
    this.getChartData();
  },
  methods: {
    getProductInfo() {
      this.sendReq('I0043', {
        ProductId: this.product.code
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          var priceInfo = res.data.data;
          this.product.name = priceInfo.ProductName;
          this.price.open = priceInfo.TodayOpen;
          this.price.close = priceInfo.LastClose;
          this.price.now = priceInfo.NowPrice;
          this.price.highest = priceInfo.HighPrice;
          this.price.lowest = priceInfo.LowPrice;
        }
      })
      .catch((err) => { console.log(err); });
    },
    getChartData() {
      Highcharts.setOptions({
        lang: {
          rangeSelectorZoom: ''
        }
      });
      this.sendReq('I0035', {
        kType: this.selected,
        productId: this.product.code,
        rows: 250
      })
      .then((res) => {
        console.log(res);
        if(res.data.StatusCode == 0){
          if(this.options.series == null) {
            this.options.series = [{
              type : 'candlestick',
              name : 'AAPL Stock Price',
              data : [],
              color: 'green',
              lineColor: 'green',
              upColor: 'red',
              upLineColor: 'red'
            }];
          }
          this.options.rangeSelector.selected = Number(this.selected);
          res.data.data.forEach((item, index) => {
            var timeFormat = '0000-00-00 00:00:00';
            var time = new Date(item.LineTime + timeFormat.substr(item.LineTime.length)).getTime() + 8*60*60*1000;
            this.options.series[0].data.unshift([time, item.OpenPrice, item.HighPrice, item.LowPrice, item.ClosePrice]);
          });
          console.log(this.options.series[0].data);
          this.initChart();
        }
      })
      .catch((err) => { console.log(err); });
    },
    initChart() {
      this.chart = new Highcharts.stockChart(this.$refs.chart, this.options);
    }
  },
  created() {
    this.product.code = this.$route.params.name;
    this.getProductInfo();
  }
}
</script>

<style lang="scss">
@import "./style/mixin";
.product-container {
  height: 100%;
  background-color: #fff;
}
.product-head {
  position: static;
  text-align: center;
  .name {
    padding-top: 5px;
    font-size: 18px;
  }
  .code {
    font-size: 14px;
  }
  .icon.icon-l {
    left: 10px;
  }
  .icon.icon-r {
    right: 50px;
    top: 10px;
  }
  .select {
    color: #fff;
    border-color: #fff;
    white-space: nowrap;
    height: 30px;
  }
}
.product-data-container {
  background-color: $headBgColor;
  color: #fff;
  .product-data-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 20px 0;
    .price {
      font-size: 48px;
    }
    .last-data {
      font-size: 14px;
      span {
        margin-right: 20px;
      }
    }
    .product-data-item {
      p {
        margin-bottom: 10px;
      }
    }
  }
}
.product-chart {
  .chart-navbar {
    position: static;
    background-color: #fff;
  }
  .chart {
    min-width:400px;
    height: 400px;
    background-color: #ff0000;
  }
}
</style>


